@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;

}
.header__nav {
    display: flex;
    justify-content: flex-end;
    padding: 0 10px;

}

.header {
    background-color: rgba(59, 30, 192, 0.835);   
}

.header .show {
    flex-direction: column;
     align-items:space-around;
    justify-content: space-around;
}

.header .nav__link__simple {
    text-decoration: none;
    color:rgb(241, 238, 229);

}

.header .nav__link__enter,.header .nav__link__out {
text-decoration: none;
color:rgb(241, 238, 229);
}

.header .nav-link>a:hover {
    text-shadow: 1px 0 black;
    transition: text-shadow 0.5s;

    color:black;
}




@media screen and (max-width:992px){

    .header .header__nav  {
        flex-direction: column;
        align-items:space-around;
    }

    .header .header__nav__right {
        min-width: 90vw;
        padding: 0;
    }
    .header  .show .header__nav__right>.nav-link {
        min-width: 90vw;
       border-bottom: 1px solid gray;
      
    }

    .nav-link{
        text-align: center;
    }

}
    

body {
  background-color: #EAEAEA;
} 

.coin__app {
  padding-top: 3em;
  color: rgb(46, 43, 43);
}


.coin__search {
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.coin__text {
  margin-bottom: 1rem;
  text-align: center;
}

.coin__input {
  padding-left: 16px;
  max-width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #0e10b3 0%,
    #88229688 48%,
    #373ae7 100%
  );

  color: #f3eaea;
}

.coin__input::-webkit-input-placeholder {
  color: #f3eaea8f;
}

.coin__input:-ms-input-placeholder {
  color: #f3eaea8f;
}

.coin__input::placeholder {
  color: #f3eaea8f;
} 


.table__header th>h5 {
  display: flex;
  justify-content: space-around;
}
.coin__row td>p {
  display: flex;
  justify-content: space-around;
}
.coin__name {
  font-weight: bold;
  white-space: nowrap;
  min-width: 85px;
  max-width: 200px;
  text-align: center;
}
.coin__name>p {
  min-width: 85px;
  max-width: 200px;
}
.coin__img {
  min-height: 100%;
  max-width: 30px;

 
}
  
  .coin__img img {
    max-height: 30px;
    max-width: 30px;
    margin-right: 1em;
    
  }
  
  .coin__symbol {
    text-transform: uppercase;
  }


  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
   

@media screen and (max-width:1365px) {
  .coin__name {
    max-width: 100px;
    white-space:pre-line;
   
  }
    .coin__name>p {
      min-width: 85px;
      max-width:100px;
    }
}
  

